.NavParent{
    width:100vw;
    top:0;
    left:0;
    right:0;
    overflow-x: auto;
}

.NavChild{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding:0.2cm 0.3cm;
}

.Menu{
    display: flex;
    flex-direction: row;
    column-gap: 0.5cm;
    align-items: center;
}

.LogoImg{
    width:6cm;
    height:auto;
}

.item{
    color:black;
    font-size: 16px;
}

.item:hover{
    color:rgb(255, 0, 0);
    text-decoration: underline;
}